.App {
  text-align: center;
}

.nav-bar {
  background-color: #00b9ef;
  color: white;
  text-align: left;
  padding: 20px;
}

.container span, h4 {
  display: inline-block;
}


.container span {
  cursor: pointer;
}

.container {
  text-align: left;;
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  position: relative;
}

.exit-btn {
  position: absolute;
  top: 0;
  right: 0;
  padding: 5px 10px;
  cursor: pointer;
}

.container h4, p {
  margin: 0;
  padding: 10px 30px;
}

.modal {
  background-color: #f6bbbd;
  margin-top: 30px;
  width: 80%;
  margin: 30px auto;
  border-radius: 3px;
}

.title {
  cursor: pointer;
}

.modal-title {
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  margin:0;
  padding: 10px;
  width: 80%;
}

.input-box > input,button {
  margin-top: 30px;
  height: 30px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
